import React, { useState } from 'react';
import Countdown from 'react-countdown';
import axios from 'axios';
import './App.css';

const Completionist = () => <span>¿Qué haces mirando esto? Andá a la quinta!</span>;
 
// Renderer callback with condition
const renderer = ({ days, hours, minutes, seconds, completed }) => {
  if (completed) {
    // Render a completed state
    return <Completionist />;
  } else {
    // Render a countdown
    const text = `${days} días, ${hours} horas, ${minutes} minutos y ${seconds} segundos.`;
    return <span>{text}</span>;
  }
};

function App() {
  const [clima, setClima] = useState('');

  axios.get('http://api.openweathermap.org/data/2.5/weather?q=Canuelas,Buenos%20aires&appid=3bb1ce5cdda41cf78d642217d1d40e88&units=metric&lang=es')
    .then(res => {
      setClima({ 
        temp: res.data.main.temp,
        how: res.data.weather[0].description,
        termica: res.data.main.feels_like
      });
    });
  
    console.log(clima)
  
  return (
    <div className="App">
      <header className="App-header">
        <h1>
          Cuadrazos, Cañuelas en:
        </h1>
        <Countdown date={'2020-12-04T16:00:00'} renderer={renderer}/>
        { clima ?
          <span>¿Cómo está en Cañuelas ahora? Hacen {clima.temp} grados, {clima.how} y la termica es de {clima.termica}</span>
          : <span>¿Cómo está en Cañuelas ahora? Ni puta idea.</span>
        }
        <div className='Item'>
          <a
            className="link"
            href="https://docs.google.com/spreadsheets/d/1_4J21WzATOdeTBdpbAU_euGrJKQCbGvxqC9EdgN2TZQ/edit?usp=sharing"
            target="_blank"
            rel="noopener noreferrer"
          >
            <div className='Tano-logo'></div>
          </a>
          <a
          className="link"
          href="https://discord.gg/Pbbn2CE"
          target="_blank"
          rel="noopener noreferrer"
        >
          <div className='Discord-logo'></div>
        </a>
        </div>
      </header>
    </div>
  );
}

export default App;
